* {
    box-sizing: border-box;
}

html {
    font-size: .2vw;
}

@media (min-width: 500px) {
    html {
        font-size: 1px;
    }
}

body {
    background: black;
    font-size: 16rem;
    font-family: "Helvetica Neue", Helvetica, sans-serif;
    margin: 10rem;
    padding: 0;
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
}

main {
    display: flex;
    flex-direction: column;
    height: calc(100vh - 20rem);
    width: 480rem;
    margin: 0 auto;
}

.board {
    height: 480rem;
    padding-bottom: 100%;
    position: relative;
}

.board__square {
        float: left;
        height: calc(480rem / 7);
        width: calc(480rem / 7);
    }

.board__title {
        background: #000;
        color: white;
        font-size: 3.3em;
        font-weight: bold;
        grid-area: 3 / 2 / span 1 / span 3;
        line-height: 1.34em;
        height: 100%;
        position: absolute;
        text-align: center;
        text-transform: uppercase;
        width: 100%;
        z-index: 5;
    }

.square {
    image-rendering: -moz-crisp-edges;
    image-rendering: pixelated;
    opacity: .9;
    -webkit-transition: opacity 500ms, -webkit-transform 500ms;
    transition: opacity 500ms, -webkit-transform 500ms;
    transition: transform 500ms, opacity 500ms;
    transition: transform 500ms, opacity 500ms, -webkit-transform 500ms;
}

.square::before {
        background: 50% 0 / 300% url(data:image/png;base64,) no-repeat;
        content: '';
        display: block;
        padding-bottom: 100%;
    }

.square_active {
        opacity: 1;
        -webkit-transform: scale(1.2, 1.2);
                transform: scale(1.2, 1.2);
    }

.square_inactive {
        opacity: .6;
    }

.square_type_0::before {
        background-position: 0 0;
        -webkit-filter: hue-rotate(calc(var(--hue0) * 1deg));
                filter: hue-rotate(calc(var(--hue0) * 1deg));
    }

.square_type_1::before {
        background-position: 50% 0;
        -webkit-filter: hue-rotate(calc(var(--hue1) * 1deg));
                filter: hue-rotate(calc(var(--hue1) * 1deg));
    }

.square_type_2::before {
        background-position: 100% 0;
        -webkit-filter: hue-rotate(calc(var(--hue2) * 1deg));
                filter: hue-rotate(calc(var(--hue2) * 1deg));
    }

.square_type_3::before {
        background: none;
    }

.square_hidden {
        opacity: 0;
        -webkit-transform: scale(0, 0);
                transform: scale(0, 0);
    }

.square_ready::before {
        animation: jump 300ms infinite alternate-reverse;
    }

.menu {
    align-content: space-between;
    display: flex;
    flex-grow: 1;
    flex-wrap: wrap;
    font-size: 1rem;
    justify-content: space-between;
    margin: 8rem;
}

.menu_small {
        align-content: flex-start;
        align-items: flex-start;
    }

.menu_main {
        max-height: 240rem;
    }

.menu__block {
        flex-basis: 100%;
    }

.action {
    color: #ccc;
    cursor: pointer;
    font-size: 35rem;
    margin: 0 0 10rem;
    text-transform: uppercase;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.action a {
        color: inherit;
        text-decoration: none;
    }

.stat__title {
        color: #ccc;
        text-align: left;
        text-transform: uppercase;
        font-size: 30rem;
    }

.stat__value {
        color: white;
        font-size: 42rem;
        text-align: right;
    }

.stat__square {
        background: 50% 0 / 300% url(data:image/png;base64,) no-repeat;
        background-position-x: 150%;
        float: right;
        height: 42rem;
        image-rendering: -moz-crisp-edges;
        image-rendering: pixelated;
        margin-right: -4rem;
        margin-top: 4rem;
        width: 42rem;
    }

.stat__square_type_0 {
            background-position-x: 0;
            -webkit-filter: hue-rotate(calc(var(--hue0) * 1deg));
                    filter: hue-rotate(calc(var(--hue0) * 1deg));
        }

.stat__square_type_1 {
            background-position-x: 50%;
            -webkit-filter: hue-rotate(calc(var(--hue1) * 1deg));
                    filter: hue-rotate(calc(var(--hue1) * 1deg));
        }

.stat__square_type_2 {
            background-position-x: 100%;
            -webkit-filter: hue-rotate(calc(var(--hue2) * 1deg));
                    filter: hue-rotate(calc(var(--hue2) * 1deg));
        }

.message {
    color: #ccc;
    font-size: 35rem;
}

.slider {
    display: flex;
    margin-bottom: 20rem;
}

.slider__square {
        width: 40rem;
    }

.slider__bar-border {
        flex-grow: 1;
        height: 40rem;
        border: 4rem solid #ccc;
        margin-left: 10rem;
        position: relative;
    }

.slider__bar {
        background-color: #ccc;
        height: 100%;
    }

.options-headline {
    color: #ccc;
    cursor: pointer;
    font-size: 35rem;
    margin-bottom: 20rem;
    text-transform: uppercase;
}

@-webkit-keyframes jump {
    from {
        -webkit-transform: translateY(.1em);
                transform: translateY(.1em);
    }

    to {
        -webkit-transform: translateY(-.1em);
                transform: translateY(-.1em);
    }
}

@keyframes jump {
    from {
        -webkit-transform: translateY(.1em);
                transform: translateY(.1em);
    }

    to {
        -webkit-transform: translateY(-.1em);
                transform: translateY(-.1em);
    }
}

